/* flex */
.flex {
  display: flex;
}
.flex-center {
  justify-content: center;
  align-items: center;
}
.flex-bt {
  justify-content: space-between;
}
.flex-ais {
  align-items: flex-start;
}
.flex-aie {
  align-items: flex-end;
}
.flex-wrap {
  flex-wrap: wrap;
}
.flex-aic {
  align-items: center;
}
.flex-column {
  flex-direction: column;
}
.flex-1 {
  flex: 1;
}
.flex-2 {
  flex: 2;
}
/* 圆形 */
.br {
  border-radius: 50%;
}
/* 浮动 */
.fl {
  float: left;
}
.fr {
  float: right;
}
.mt10 {
  margin-top: 10px;
}
.mb10 {
  margin-bottom: 10px;
}
.bgfff {
  background: #fff;
}
.bsbb {
  box-sizing: border-box;
}
.ell3 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}



